const firestore=getFirestore();

export const listAllContent = async (schoolid) => {
  try {
    let querySnapshot = null;
    if (schoolid) {
      querySnapshot = await app.firestore().collection('schools').doc(schoolid).collection('contents').orderBy('create_at', 'desc').get();
    } else {
      querySnapshot = await app.firestore().collection('contents').orderBy('create_at', 'desc').get();
    }
    const content = querySnapshot.docs.map((doc) => {
      return {
        id: doc.id,
        ...doc.data()
      };
    });
    return content;
  } catch (err) {
    console.log(err);
  }
};


export const addContent = async (values, image) => {

  try {
  //   var imageBuffer = Buffer.from(image);
    let detail = ' <news content-type=\'news\'>' + values.detail + '</news>';
    delete values.detail;
    values.content_type = 'news';
    values.create_at = firestore.FieldValue.serverTimestamp();
    let contentRef = await app.firestore().collection('contents').add(values);

    let content = await contentRef.get();
    console.log('done content:' + content.id);
    // contentRef.collection('detail').doc('content').set({ data: detail });
    let contentData = content.data();
    var uploadDetail = app.storage().ref().child('public/contents/'+content.id).putString(detail);
    uploadDetail.on(getStorage().TaskEvent.STATE_CHANGED, // or 'state_changed'
      function(snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case getStorage().TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case getStorage().TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function(error) {

        switch (error.code) {
          case 'storage/unauthorized':
            console.log('unauthorized');
            break;
          case 'storage/canceled':
            console.log('cancel');
            break;
          case 'storage/unknown':

            console.log('unkonwn');
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function() {
        // Upload completed successfully, now we can get the download URL
        uploadDetail.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          console.log('File available at', downloadURL);
          contentRef.collection('detail').doc('content').set({data: detail,
            file: 'public/contents/'+content.id,
            content_url: downloadURL });
        });
      });
    // .then((snapshot) => {
    //   console.log(snapshot);
    //   contentRef.collection('detail').doc('content').set({ file: 'public/contents/'+content.id });
    // });

    if (typeof (image) == 'undefined') {
      return;
    }
    var metadata = {
      contentType: 'image/jpeg'
    };
    let cover_url = 'public/images/content/' + content.id;
    let cover_app_url = 'public/images/content/app' + content.id;
    var storageRef = app.storage().ref();
    var uploadTask = storageRef.child(cover_url).put(image, metadata);
    uploadTask.on(getStorage().TaskEvent.STATE_CHANGED, // or 'state_changed'
      function(snapshot) {
        // Get task progress, including the number of bytes uploaded and the total number of bytes to be uploaded
        var progress = (snapshot.bytesTransferred / snapshot.totalBytes) * 100;
        console.log('Upload cover is ' + progress + '% done');
        switch (snapshot.state) {
          case getStorage().TaskState.PAUSED: // or 'paused'
            console.log('Upload cover is paused');
            break;
          case getStorage().TaskState.RUNNING: // or 'running'
            console.log('Upload cover is running');
            break;
        }
      },
      function(error) {

        switch (error.code) {
          case 'storage/unauthorized':
            console.log('unauthorized');
            break;
          case 'storage/canceled':
            console.log('cancel');
            break;
          case 'storage/unknown':

            console.log('unkonwn');
            // Unknown error occurred, inspect error.serverResponse
            break;
        }
      },
      function() {
        // Upload completed successfully, now we can get the download URL
        uploadTask.snapshot.ref.getDownloadURL().then(function(downloadURL) {
          console.log('File available at', downloadURL);
          contentData.cover = downloadURL;
          app.firestore().collection('contents').doc(content.id).update(contentData);
        });
      });
  } catch (err) {
    console.log(err);
  }
};

export const deleteContents = (ids) => {
  try {
    ids.map((id) => {
      let cover_url = 'public/images/content/' + id;
      var storageRef = app.storage().ref();
      storageRef.child(cover_url).getDownloadURL().then(onResolve, onReject);

      function onResolve(foundURL) {
        storageRef.child(cover_url).delete();
      }

      function onReject(error) {
        console.log(error.code);
      }

      app.firestore().collection('contents').doc(id).collection('detail').doc('content').delete();
      app.firestore().collection('contents').doc(id).delete();
    });
  } catch (err) {
    console.log(err);
  }
};

export const sendNotify_Content = async(contentsId)=>{
  await getMyBody(contentsId, function(err, body) {
    if (err) {
      console.log(err);
      return{
        statusCode:500,
        message:"Gửi notify thất bại"
      }
    } else {
      console.log(body);
      return {
        statusCode:200,
        message:'Gửi notify thành công'
      }
    }
  });
};
async function getMyBody(contentsId,callback){
  console.log(contentsId);
  const contentRef =  app.firestore().collection("contents").doc(contentsId);
  const contentInfo = await contentRef.get();
  if(contentInfo.exists){
    const contents = contentInfo.data();
    const topics = contents.tags;
    if(topics.length>0) {
      let topic = [];
      topics.forEach((item, index) => {
        if (topics.length >= 5) {
          if (index < 5) {
            if (index == 4) {
              topic += "'" + item + "'" + " in topics ";
            } else {
              topic += "'" + item + "'" + " in topics || ";
            }
          }
        } else {
          console.log(topic.length);
          console.log("topic.length");
          if (index < 5) {
            if (index + 1 == topics.length) {
              topic += "'" + item + "'" + " in topics ";
            } else {
              topic += "'" + item + "'" + " in topics || ";
            }
          }
        }
      });
      // const auth_key: any =
      //     "AAAAQx2VGH0:APA91bFH8hD3jePoKNsCShHONYlQslhMGIZke3YzFaO-JysZDbYaDiypik7Esy4BdhTYa34LjvSGLgqGNRGk7_JnUBm59yaAPSCmXK5wAZBJsztBDPCBKTIrjzIFv7gH3ebXDGRk0q_f";
      const headers = {
        "User-Agent":
          "Mozilla/5.0 (Macintosh; Intel Mac OS X 10.8; rv:24.0) Gecko/20100101 Firefox/24.0",
        "Content-Type": "application/json",
        Authorization: "key="+process.env.REACT_APP_FIREBASE_CLOUD_MESSAGE_KEY,
        // Authorization: "key=AAAAQx2VGH0:APA91bFH8hD3jePoKNsCShHONYlQslhMGIZke3YzFaO-JysZDbYaDiypik7Esy4BdhTYa34LjvSGLgqGNRGk7_JnUBm59yaAPSCmXK5wAZBJsztBDPCBKTIrjzIFv7gH3ebXDGRk0q_f",
      };
      const params = {
        condition: topic,
        // to:"dzzEEKhyS86WXRqgm7SMqp:APA91bHsfLjBOQ-0eNrQV1gi43FQ95mF8w7STwDC2aln5f9n5ztI4PskbXPGimpaS-dEoBR82SY3nZvahF7AzRe-2aTlrk9bw55tDF-9KRU1mMLG2DCVjPJkbipoAR4a7lQME8iD7H-y",
        priority: "high",
        notification: {
          body: contents.title,
          title: "Hbon news",
          "sound": "default",
          "image":contents.cover
        },
        apns: {
          "payload": {
            "aps": {
              "mutable-content": 1
            }
          },
          "fcm_options": {
            "image": contents.cover
          }
        },
        data: {
          click_action: "FLUTTER_NOTIFICATION_CLICK",
          deep_link:  process.env.REACT_APP_DOMAIN_VIEWER+"/news_detail?content_id=" + contentsId + "&news_title=" + encodeURIComponent(contents.title),
          // deep_link:  "https://test.dschool.vn/news_detail?content_id=" + contentsId + "&news_title=" + encodeURIComponent(contents.title),
          body: contents.title,
          title: "Hbon news",
          content_type: "news",
        },
      };
      request(
        {
          method: "POST",
          url: "https://fcm.googleapis.com/fcm/send",
          body: params,
          headers: headers,
          json: true,
        },
        function (error, response, body) {
          console.log(body);
          console.log(response);
          console.log(response.statusCode);
          console.log(response.request);
          if (error || response.statusCode !== 200) {
            console.log(body);
            return callback(error || { statusCode: response.statusCode });
          }
          callback(null, body);
        });
    }
  }
}

