export const getContentApi = async (id) => {
  try {
    let querySnapshot = await app.firestore().collection('contents').doc(id).get();
    const content = querySnapshot.data();
    content.id = id;
    let querySnapshotDetail = await app.firestore().collection('contents').doc(id).collection('detail').doc('content').get();
    const contentDetail = querySnapshotDetail.data();

    console.log(contentDetail.content_url);
    const detail = await fetch(contentDetail.content_url)
      .then( r => r.text() );
      // .then( t => {
      //   content.detail = t;
      // } );
    console.log(detail);
    content.detail = detail;
    return content;
  } catch (err) {
    console.log(err);
  }
};

