export const downloadReport = async(contentId)=>{
  console.log(contentId);
  var jsonCustomers = [];
  const embebRef = app.firestore().collection('contents').doc(contentId).collection('embedded_submissions');
  const snapshot = await embebRef.get();
  snapshot.forEach(async (doc) => {
    // console.log(doc.id, '=>', doc.data());
    const submitData = doc.data();
    const resultRef = app.firestore().collection('contents').doc(contentId).collection('embedded_submissions').doc(doc.id).collection('quizzes_submissions');
    const resultData = await resultRef.get();
    resultData.forEach(async(result) => {
      let logs = result.data().result;
      submitData['logs-'+result.id] = logs.toString();
      submitData['results-'+result.id] = logs.filter(function(value){
        return value === true;
      }).length ;
    });
    jsonCustomers.push(submitData);
    console.log(submitData);
  });
  const competition = await app.firestore().collection('contents').doc(contentId).get();
  const competitionData = competition.data();
  setTimeout(function(){
    const fileType = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
    const fileExtension = '.xlsx';

    const fileName = competitionData.title;
    const ws = XLSX.utils.json_to_sheet(jsonCustomers);
    const wb = { Sheets: { 'data': ws }, SheetNames: ['data'] };
    const excelBuffer = XLSX.write(wb, { bookType: 'xlsx', type: 'array' });
    const data = new Blob([excelBuffer], {type: fileType});
    FileSaver.saveAs(data, fileName + fileExtension);
  }, 30000);
};
