import React, { useState } from 'react';
import { Link as RouterLink } from 'react-router-dom';
import clsx from 'clsx';
import PropTypes from 'prop-types';
import PerfectScrollbar from 'react-perfect-scrollbar';
import {
  Box,
  Button,
  Card,
  Checkbox,
  Divider,
  IconButton,
  InputAdornment,
  SvgIcon,
  Tab,
  Table,
  TableBody,
  TableCell,
  TableHead,
  TablePagination,
  TableRow,
  Tabs,
  TextField,
  makeStyles
} from '@material-ui/core';
import {
  Edit as EditIcon,
  ArrowRight as ArrowRightIcon,
  Search as SearchIcon
} from 'react-feather';
import getInitials from 'src/utils/getInitials';
import { useHistory } from 'react-router-dom';
import {deleteSchoolContents} from '../../../../api/contents.js';
const tabs = [
  {
    value: 'all',
    label: 'Tất cả'
  }
];

const sortOptions = [
  {
    value: 'updatedAt|desc',
    label: 'Last update (newest first)'
  },
  {
    value: 'updatedAt|asc',
    label: 'Last update (oldest first)'
  },
  {
    value: 'name|desc',
    label: 'Tên (giảm)'
  },
  {
    value: 'name|asc',
    label: 'Tên (tăng)'
  }
];

const applyFilters = (contents, query, filters) => {
  return contents.filter((content) => {
    let matches = true;

    if (query) {
      const properties = ['title', 'description'];
      let containsQuery = false;

      properties.forEach((property) => {
        if ((property in content) && content[property].toLowerCase().includes(query.toLowerCase())) {
          containsQuery = true;
        }
      });

      if (!containsQuery) {
        matches = false;
      }
    }

    Object.keys(filters).forEach((key) => {
      const value = filters[key];

      if (value && content[key] !== value) {
        matches = false;
      }
    });

    return matches;
  });
};

const applyPagination = (contents, page, limit) => {
  return contents.slice(page * limit, page * limit + limit);
};

const descendingComparator = (a, b, orderBy) => {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }

  if (b[orderBy] > a[orderBy]) {
    return 1;
  }

  return 0;
};

const getComparator = (order, orderBy) => {
  return order === 'desc'
    ? (a, b) => descendingComparator(a, b, orderBy)
    : (a, b) => -descendingComparator(a, b, orderBy);
};

const applySort = (contents, sort) => {
  const [orderBy, order] = sort.split('|');
  const comparator = getComparator(order, orderBy);
  const stabilizedThis = contents.map((el, index) => [el, index]);

  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);

    if (order !== 0) return order;

    return a[1] - b[1];
  });

  return stabilizedThis.map((el) => el[0]);
};

const useStyles = makeStyles((theme) => ({
  root: {},
  queryField: {
    width: 500
  },
  bulkOperations: {
    position: 'relative'
  },
  bulkActions: {
    paddingLeft: 4,
    paddingRight: 4,
    marginTop: 6,
    position: 'absolute',
    width: '100%',
    zIndex: 2,
    backgroundColor: theme.palette.background.default
  },
  bulkAction: {
    marginLeft: theme.spacing(2)
  },
  avatar: {
    height: 42,
    width: 42,
    marginRight: theme.spacing(1)
  }
}));

const Results = ({
  className,
  contents,
  ...rest
}) => {
  const history = useHistory();
  const classes = useStyles();
  const [currentTab, setCurrentTab] = useState('all');
  const [selectedContents, setSelectedContents] = useState([]);
  const [page, setPage] = useState(0);
  const [limit, setLimit] = useState(10);
  const [query, setQuery] = useState('');
  const [sort, setSort] = useState(sortOptions[0].value);
  const [filters, setFilters] = useState({
    hasAcceptedMarketing: null,
    isProspect: null,
    isReturning: null
  });

  const handleTabsChange = (event, value) => {
    const updatedFilters = {
      ...filters,
      hasAcceptedMarketing: null,
      isProspect: null,
      isReturning: null
    };

    if (value !== 'all') {
      updatedFilters[value] = true;
    }

    setFilters(updatedFilters);
    setSelectedContents([]);
    setCurrentTab(value);
  };

  const handleQueryChange = (event) => {
    event.persist();
    setQuery(event.target.value);
  };

  const handleSortChange = (event) => {
    event.persist();
    setSort(event.target.value);
  };

  const handleSelectAllContents = (event) => {
    setSelectedContents(event.target.checked
      ? contents.map((content) => content.id)
      : []);
  };

  const handleSelectOneContent = (event, contentId) => {
    if (!selectedContents.includes(contentId)) {
      setSelectedContents((prevSelected) => [...prevSelected, contentId]);
    } else {
      setSelectedContents((prevSelected) => prevSelected.filter((id) => id !== contentId));
    }
  };

  const handlePageChange = (event, newPage) => {
    setPage(newPage);
  };

  const handleLimitChange = (event) => {
    setLimit(parseInt(event.target.value));
  };

  const handleDelete = () => {
      let schoolid = localStorage.getItem('schoolid');
      deleteSchoolContents(selectedContents,schoolid);
      history.push('/hbon/management/contents');
    };

  const filteredContents = applyFilters(contents, query, filters);
  const sortedContents = applySort(filteredContents, sort);
  const paginatedContents = applyPagination(sortedContents, page, limit);
  const enableBulkOperations = selectedContents.length > 0;
  const selectedSomeContents = selectedContents.length > 0 && selectedContents.length < contents.length;
  const selectedAllContents = selectedContents.length === contents.length;

  return (
    <Card
      className={clsx(classes.root, className)}
      {...rest}
    >
      <Tabs
        onChange={handleTabsChange}
        scrollButtons="auto"
        textColor="secondary"
        value={currentTab}
        variant="scrollable"
      >
        {tabs.map((tab) => (
          <Tab
            key={tab.value}
            value={tab.value}
            label={tab.label}
          />
        ))}
      </Tabs>
      <Divider />
      <Box
        p={2}
        minHeight={56}
        display="flex"
        alignItems="center"
      >
        <TextField
          className={classes.queryField}
          InputProps={{
            startAdornment: (
              <InputAdornment position="start">
                <SvgIcon
                  fontSize="small"
                  color="action"
                >
                  <SearchIcon />
                </SvgIcon>
              </InputAdornment>
            )
          }}
          onChange={handleQueryChange}
          placeholder="Tìm kiếm"
          value={query}
          variant="outlined"
        />
        <Box flexGrow={1} />
        <TextField
          label="Sắp xếp"
          name="sort"
          onChange={handleSortChange}
          select
          SelectProps={{ native: true }}
          value={sort}
          variant="outlined"
        >
          {sortOptions.map((option) => (
            <option
              key={option.value}
              value={option.value}
            >
              {option.label}
            </option>
          ))}
        </TextField>
      </Box>
      {enableBulkOperations && (
        <div className={classes.bulkOperations}>
          <div className={classes.bulkActions}>
            <Checkbox
              checked={selectedAllContents}
              indeterminate={selectedSomeContents}
              onChange={handleSelectAllContents}
            />
            <Button
              variant="outlined"
              className={classes.bulkAction}
              onClick={handleDelete}
            >
              Xóa
            </Button>
          </div>
        </div>
      )}
      <PerfectScrollbar>
        <Box minWidth={700}>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell padding="checkbox">
                  <Checkbox
                    checked={selectedAllContents}
                    indeterminate={selectedSomeContents}
                    onChange={handleSelectAllContents}
                  />
                </TableCell>
                <TableCell>
                  Loại Content
                </TableCell>
                <TableCell>
                  Tiêu đề (title)
                </TableCell>
                <TableCell>
                  Cover
                </TableCell>
                <TableCell>
                  Mô tả (description)
                </TableCell>
                <TableCell>
                  Roles
                </TableCell>
                <TableCell>
                  Tags
                </TableCell>
                <TableCell>
                  Votes
                </TableCell>
                <TableCell align="right">
                  Actions
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {paginatedContents.map((content) => {
                let upvotes = null;
                let downvotes = null;
                if("reactions" in content){
                  upvotes = content.reactions.upvotes
                  downvotes = content.reactions.downvotes  
                }
                
                const isContentSelected = selectedContents.includes(content.id);
                
                if(!("roles" in content)){
                  content.roles=[""];
                }
                if(!("tags" in content)){
                  content.tags=[""];
                }
                return (
                  <TableRow
                    hover
                    key={content.id}
                    selected={isContentSelected}
                  >
                    <TableCell padding="checkbox">
                      <Checkbox
                        checked={isContentSelected}
                        onChange={(event) => handleSelectOneContent(event, content.id)}
                        value={isContentSelected}
                      />
                    </TableCell>
                    <TableCell>
                      <Box
                        display="flex"
                        alignItems="center"
                      >
                            {content.content_type}
                      </Box>
                    </TableCell>
                    <TableCell>
                            {content.title}
                    </TableCell>
                    <TableCell>
                            {content.cover}
                    </TableCell>
                    <TableCell>
                            {content.description}
                    </TableCell>
                    <TableCell>
                    {content.roles.map((s) => 
                      (
                        <span key={s}
                        >
                          {s}, 
                        </span>
                      )
                    )}
                    </TableCell>
                    <TableCell>
                    {content.tags.map((s) => 
                      (
                        <span key={s}
                        >
                          {s}, 
                        </span>
                      )
                    )}
                    </TableCell>
                    <TableCell>
                    
                      <span>{upvotes || null}</span>,
                      <span>{downvotes|| null}</span>
                      
                    </TableCell>
                    <TableCell align="right">
                      <IconButton
                        component={RouterLink}
                        to={"/school/management/contents/edit/"+ content.id}
                      >
                        <SvgIcon fontSize="small">
                          <EditIcon />
                        </SvgIcon>
                      </IconButton>
                      
                    </TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </Box>
      </PerfectScrollbar>
      <TablePagination
        component="div"
        count={filteredContents.length}
        onChangePage={handlePageChange}
        onChangeRowsPerPage={handleLimitChange}
        page={page}
        rowsPerPage={limit}
        rowsPerPageOptions={[5, 10, 25]}
      />
    </Card>
  );
};

Results.propTypes = {
  className: PropTypes.string,
  contents: PropTypes.array.isRequired
};

Results.defaultProps = {
  contents: []
};

export default Results;
